.brand {
  &__grid {
    display: grid;
    justify-items: center;
    grid-gap: 3rem 4rem;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    margin: 0 auto;
    max-width: 1800px;
    text-align: center;
  }
}

@media (max-width: $mobile) {
  .brand {
    &__grid {
      img:nth-child(odd) {
        display: none;
      }
    }
  }
}
