.request {
  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__form {
    margin: 60px 120px;

    &-request {
      margin: 0 auto;
      max-width: fit-content;
    }

    &-list {
      position: relative;

      li {
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }

    &-submit {
      border-radius: 12px;
      width: 100%;
      height: 90px;
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      text-align: center;
      color: $color-brand;
      background-color: $color-brand-light-1;
      transition: all 300ms ease-in-out;

      &:not(:disabled) {
        color: #fff;
        background-color: $color-brand;

        &:hover {
          color: #fff;
          background-color: $color-brand-dark-7;
        }

        &:active {
          color: #fff;
          background-color: $color-brand-light-5;
        }
      }
    }
  }

  &__label {
    position: relative;
    transition: all 300ms ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 27px;
      bottom: 0;
      display: block;
      padding: 0 10px;
      width: auto;
      font-size: 24px;
      color: $color-gray-7;
      background-color: #fff;
      cursor: text;
    }

    &-name {
      &::before {
        content: "Ваше имя";
      }
    }

    &-tel {
      &::before {
        content: "Ваш телефон";
      }
    }

    &-select {
      &::before {
        content: "Бренд стиральной машинки";
      }
    }

    &-msg {
      &::before {
        content: "Опишите поломку";
        bottom: 240px;
      }

      &-write {
        border-color: $color-succes;

        &::before {
          content: "Опишите поломку";
          bottom: 295px;
          font-size: 16px;
          line-height: 130%;
        }
      }
    }

    &-write {
      &::before {
        bottom: 50px;
        font-size: 16px;
      }

      .request__input-req:not(.request__input-req:invalid) {
        border-color: $color-succes;
      }

      .request__input-req:invalid {
        border-color: $color-error;
      }
    }
  }

  &__input {
    outline: none;
    border: 1px solid $color-gray-7;
    border-radius: 12px;
    padding: 0 35px;
    width: 505px;
    height: 90px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    line-height: 130%;

    &-msg {
      padding-top: 35px;
      height: 300px;
    }
  }

  &__politic {
    margin: 0 auto;
    margin-top: 30px;
    max-width: fit-content;
    text-align: center;

    &-text {
      margin: 0;
      margin-bottom: 10px;
      font-size: 16px;
      line-height: 180%;
    }

    &-link {
      color: $color-gray-6;
      transition: all 300ms ease-in-out;

      &:hover {
        color: $color-brand;
      }
    }
  }
}

@media (max-width: $tablet) {
  .request {
    &__container {
      flex-direction: column;
    }

    &__image {
      width: 100%;
      max-height: 50vh;
      object-fit: cover;
    }
  }
}

@media (max-width: $mobile) {
  .request {
    &__input {
      width: 100%;
      min-width: 300px;
    }

    &__form {
      margin: 0 auto;
      padding: 30px;
      padding-top: 25px;
      width: 100%;

      &-list {
        li {
          &:not(:first-child) {
            margin-top: 20px;
          }
        }
      }

      &-request {
        margin: auto;
      }

      &-submit {
        height: 55px;
        font-size: 16px;
      }
    }

    &__image {
      max-height: 232px;
    }

    &__input {
      height: 60px;
      font-size: 16px;
      line-height: 130%;

      &-msg {
        height: 200px;
      }
    }

    &__label {
      &::before {
        font-size: 16px;
        line-height: 130%;
      }

      &-msg {
        &::before {
          bottom: 149px;
        }

        &-write {
          &::before {
            bottom: 197px;
            font-size: 12px;
          }
        }
      }

      &-write {
        &::before {
          bottom: 35px;
          font-size: 12px;
        }
      }
    }
  }
}
